@import url(./styleStars.css);

* {
  margin: 0;
  text-decoration: none;
}

.card {
  display: flex;
  flex-direction: column;
  width: 198px;
  height: 406px;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    width: 160px;
    height: 134px;
    padding-top: 32px;
    padding-left: 19px;
  }

  &__name {
    width: 166px;
    height: 36px;
    padding-left: calc(50% - 166px / 2);
    padding-top: 40px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #060b35;
  }

  &__code {
    width: 98px;
    height: 14px;
    padding-left: calc(50% - 98px / 2 - 34px);
    padding-top: 4px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
  }

  &__reviews {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 16px 16px 0;
  }

  &__number-of-reviews {
    display: flex;
    align-items: center;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    color: #060b35;
  }

  &__price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 24px 16px 0;

    .price {
      width: 31px;
      height: 18px;
      font-family: "Roboto", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }

    .money {
      width: 50px;
      height: 18px;
      font-family: "Roboto", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      text-align: right;
      color: #060b35;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 164px;
    height: 38px;
    margin: 16px;
    background: #00acdc;
    border-radius: 5px;
    font-family: "Roboto", sans-serif;
    font-style: normal;
    border: 1px solid #00acdc;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #fff;
  }

  &__button:hover {
    background: #fff;
    color: #00acdc;
  }
}
